<template style="background: white">
	<view>
		<view class="index-content">
			<view class="index-coupon has-bg-white has-pd-10 top_30">
				<view class="goods-list" v-if="couponlist.length > 0">
					<orange-goods-card-home v-for="(item,index) in couponlist" :index="index%2" :logo="logo" :is-invitation="isInvitation"
					 :tkmoney='item.tkmoney' :isEnable='isEnable' :tkmoneys='item.tkmoneys' :itemid="item.itemid" :itempic="item.itempic+'_310x310.jpg'"
					 :itemtitle="item.itemtitle" :itemprice='"¥"+item.itemprice' :itemsale='item.itemsale' :itemendprice='""+item.itemendprice'
					 :couponmoney="item.couponmoney" :item="item"></orange-goods-card-home>
				</view>
			</view>
			<!-- 悬浮上拉 -->
			<view class="scroll_top" @tap="topScrollTap" v-bind:class="[scrollTop ? 'active' : '','']" style="bottom: 56px;">
				<text class="iconfont icon-shangla"></text>
			</view>
			<!-- 加载更多提示 -->
			<view class="s-col is-col-24" v-if="couponlist.length > 0">
				<load-more :loadingType="loadingType" :contentText="contentText"></load-more>
			</view>
			<!-- 加载更多提示 -->
			<empty v-if="couponlist.length === 0" des="暂无数据" show="false"></empty>
		</view>
	</view>
</template>


<script>
    export default {
        onShareAppMessage(res) {
            return {
                title: '购物先领券，一年省一半',
                path: '/pages/index/index'
            }
        }, data() {
            return {
                logo: "../../static/img/mao.png",
                TabCur: 0,
                scrollLeft: 0,
                genderKey: "gender",
                couponlist: [],
                page: 1,
                sort: 4,
				category: [
					'推荐',
					'女装',
					'男装',
					'内衣',
					'美妆',
					'箱包配饰',
					'母婴',
					'家居家装',
					'美食',
					'数码家电',
					'运动户外',
					'聚划算',
					'天猫超市',
				],
                loadingType: 0,
                isInvitation:0,
				isEnable:'否',
				deviceValue:'',
                scrollTop: false,
                contentText: {
                    contentdown: "上拉显示更多",
                    contentrefresh: "正在加载...",
                    contentnomore: "没有更多数据了"
                }
            }
        },
        onLoad: function (e) {
			let isEnable = this.$queue.getData("isEnable")
			if (isEnable) {
				this.isEnable = isEnable;
			}
            let deviceValue = this.$queue.getData("DeviceNo");
            if (deviceValue) {
            	this.deviceValue = deviceValue;
            }
			if (e.cid) {
				this.cid = e.cid;
				uni.setNavigationBarTitle({
					title: this.category[e.cid] + "精选"
				});
				uni.showLoading({
					title: '加载中...'
				});
			}
			this.loadCouponList();
			this.$queue.getUserInfo();
        },
        onPageScroll: function (e) {
            this.scrollTop = e.scrollTop > 200;
        },
        methods: {
            topScrollTap: function () {
                uni.pageScrollTo({
                    scrollTop: 0,
                    duration: 300
                });
            },
            loadCouponList: function (type) {
				let materialId = "3786";
				if(this.cid === "0"){
					materialId = "3786";
				}
				else if(this.cid === "1"){
					materialId = "3788";
				}
				else if(this.cid === "2"){
					materialId = "3790";
				}
				else if(this.cid === "3"){
					materialId = "3787";
				}
				else if(this.cid === "4"){
					materialId = "3794";
				}
				else if(this.cid === "5"){
					materialId = "3796";
				}
				else if(this.cid === "6"){
					materialId = "3789";
				}
				else if(this.cid === "7"){
					materialId = "3792";
				}
				else if(this.cid === "8"){
					materialId = "3791";
				}
				else if(this.cid === "9"){
					materialId = "3793";
				}
				else if(this.cid === "10"){
					materialId = "3795";
				}
				else if(this.cid === "11"){
					materialId = "31371";
				}
				else if(this.cid === "12"){
					materialId = "27160";
				}
				let apiUrl = this.$Api.common.couponbestlist;
				console.log("----DENG-Begin---");
				console.log("----apiUrl----" + apiUrl);
				console.log("----materialId----" + materialId);
				console.log("----this.page----" + this.page);
				console.log("----this.deviceValue----" + this.deviceValue);
				console.log("----DENG-End---");
				
                this.loadingType = 1;
                uni.showLoading({
                    title: '加载中...'
                });
                this.$Request.get(apiUrl, {
					MaterialId: materialId,
					HasCoupon:true,
					PageNo: this.page,
					DeviceValue: this.deviceValue
				}).then(res => {
					console.log(res);
                    this.loadingType = 0;
                    if (res.Code === 1) {
                        if (this.page === 1) {
                            this.couponlist = [];
                        }
                        res.Message.forEach(d => {
							let grade = this.$queue.getData('grade');
							d.tkmoneys = (d.tkmoney * 0.7).toFixed(2);
							if (grade) {
								d.tkmoney = (d.tkmoney * parseFloat(grade)).toFixed(2);
							} else {
								d.tkmoney = (d.tkmoney * 0.3).toFixed(2);
							}
							d.itemsale = d.itemsale > 10000 ? '已售 ' + (d.itemsale / 10000).toFixed(1) + '万' : '已售 ' + d.itemsale;
                            this.couponlist.push(d);
                        })
                    } else {
                        this.loadingType = 2;
                    }
                    if (type === "Refresh") {
                        uni.stopPullDownRefresh(); // 停止刷新
                    }
                    uni.hideLoading();
                })
            },
        },
        onReachBottom: function () {
            this.page = this.page + 1;
            this.loadCouponList();
        },
        onPullDownRefresh: function () {
            this.page = 1;
            this.loadCouponList("Refresh");
        }
    }
</script>

<style>
	@import "../../static/css/index.css";
</style>
